import { css } from '@emotion/css';

export const sheetContainerStyle = (top?: boolean, bottom?: boolean): string => css({
  position: 'fixed',
  top: top ? 0 : undefined,
  bottom: bottom ? 0 : undefined,
  height: 0,
  width: '100%',
  display: 'flex',
  justifyContent: 'center',
  zIndex: 11
});
