import { allProjectsService, bottomSheetService, useRx } from 'common';
import { HashtagList } from 'components';
import React from 'react';

const resetHashtag = '#ALLPROJECTS';

export function HashtagsSheet(): JSX.Element {
  const hastags = useRx(allProjectsService.hashtags$);

  const handleClickHashtag = React.useCallback((hashtag: string) => {
    bottomSheetService.close();
    if (hashtag === resetHashtag) {
      allProjectsService.resetFilter();

      return;
    }

    allProjectsService.filterProjectsByHashtag(hashtag);
  }, []);

  return (
    <HashtagList
      hasthags={[resetHashtag, ...hastags]}
      onClickHashtag={handleClickHashtag}
    />
  );
}
